var scrollTop = 0;
$(document).ready(function () {
  $(".menu-icon").click(function () {

    var header = $(this).parents('#header');

    scrollTop = $(window).scrollTop();
    if (header.hasClass('show')) {
      header.removeClass('show');
      ModalHelper.beforeClose()
    } else {
      header.addClass('show');
      ModalHelper.afterOpen();
    }

  });
  $(".back-top-btn").click(function () {
    var scrollToptimer = setInterval(function () {
      var top = document.body.scrollTop || document.documentElement.scrollTop;
      var speed = top / 4;
      if (document.body.scrollTop != 0) {
        document.body.scrollTop -= speed;
      } else {
        document.documentElement.scrollTop -= speed;
      }
      if (top == 0) {
        clearInterval(scrollToptimer);
      }
    }, 50);
  });

  $(".current,.sub-nav-model").on("click", function () {
    modelFun()
  });
  $(".sub-nav-list").on("click", ".sub-nav-item", function (e) {
    e.stopPropagation();
  });

  function modelFun() {
    if ($(".current").hasClass('active')) {
      $(".current").removeClass('active');
      $(".sub-nav-model").hide();
      ModalHelper.beforeClose()
    } else {
      $(".current").addClass('active');

      $(".sub-nav-model").show();
      ModalHelper.afterOpen()
    }
  }

  var ModalHelper = (function (bodyCls) {
    var scrollTop;
    return {
      afterOpen: function () {
        scrollTop = document.scrollingElement.scrollTop;
        document.body.classList.add(bodyCls);
        document.body.style.top = -scrollTop + 'px';
        $("video").hide()
      },
      beforeClose: function () {
        document.body.classList.remove(bodyCls);
        // scrollTop lost after set position:fixed, restore it back.
        document.scrollingElement.scrollTop = scrollTop;
        if ($(".video-img").length > 0 && $(".video-img").css("display") == 'none') {
          $("video").show()
        }

      }
    };
  })('modal-open');

  $(function () {
    /*banner轮播图*/
    var bannerSwiper = new Swiper('#swiper-banner', {
      direction: 'horizontal',
      autoplay: {
        delay: 5000,
        disableOnInteraction: false,
      },//可选选项，自动滑动
      loop: true, // 循环模式选项
      centeredSlides: true,
      // 如果需要分页器
      pagination: {
        el: '#swiper-banner .banner-pagination',
      },
    });
    var newsSwiper = new Swiper('.news-wrap', {
      direction: 'horizontal',
      autoplay: {
        delay: 5000,
        disableOnInteraction: false,
      },
      loop: true, // 循环模式选项
      centeredSlides: true,
      slidesPerView: 1.4,
      initialSlide: 1,
      // spaceBetween : 15,
      // 如果需要分页器
      pagination: {
        el: '.news-wrap .banner-pagination',
        clickable: true,
      },
    });
    var video = document.getElementById("video");
    $('.video-img').click(function () {
      $(this).hide();
      $('#video').show();
      video.play();
    });
    if ("4" == 0) {
      $(".news").hide()
    }
  });

  document.getElementById("curYear").innerText = (new Date()).getFullYear();
  $(".menu-nav-wrap").on('click', function () {
    if ($(this).parent().hasClass('active')) {
      $(this).parent().removeClass('active')
    } else {
      $(this).parent().addClass('active').siblings().removeClass('active')
    }
  });

  $(".back-top-btn").click(function () {
    var scrollToptimer = setInterval(function () {
      var top = document.body.scrollTop || document.documentElement.scrollTop;
      var speed = top / 4;
      if (document.body.scrollTop != 0) {
        document.body.scrollTop -= speed;
      } else {
        document.documentElement.scrollTop -= speed;
      }
      if (top == 0) {
        clearInterval(scrollToptimer);
      }
    }, 50);
  });
